import "./../../../Css/Carousel-bg.css";
import Pilt from './../Pilt';
import PropTypes from 'prop-types';

const TulemasEn = (props) => {
  return (
    <div>
      <Jumbotron fluid className="carousel-bg">
        <Container fluid>
          <div className="row">
            <div className="col-lg-6 mb-lg-5">
              <h3 className="suurem-pealkiri m-2">Soon in the club</h3>
              <UritusedCarousel />
            </div>

            <div className="col-md-10 col-lg-5 ml-md-5 ml-lg-1 text-left align-self-center mt-5">
              <h3 className="suurem-pealkiri p-2">Jutt rituste kohta</h3>
              <h5 className="viksem-pealkiri p-2">Natukene pikem jutt - kokkuvte. Paarkmmend sna tulevaste rituste ja klubi kohta.</h5>
              <p className="tekst p-2">rituste korraldamine Valgas. Kes on esinejad ja publik? Kuidas kujuneb vlja programm? Kustkohast on prit esinejad.
  Mitu sndmust aastas toimus? Mis toimub suvel? Mis muusikastiilid? Mis on programmi koostamisel thtsaim? Kas programmil on mingisugused piirid? Mida teha kui tahaks esineda vms.? Mrksnad, mida viks kasutada: ritused, Valga, muusika, bndid,
</p>
            </div>

          </div>
          <div className="mt-lg-5">
            <Pilt />
          </div>
        </Container>
      </Jumbotron>
    </div>
  );
};

export default TulemasEn;

Jumbotron.propTypes = {
  // Pass in a Component to override default element
  tag: PropTypes.oneOfType([PropTypes.func, PropTypes.string]),
  fluid: PropTypes.bool,
  className: PropTypes.string
};